  margin: auto;
  line-height: 50px;
  text-align: center;
  background: #ffffff;
  border-radius: 5px;
  height: 50px;
  margin-top: 37px;
}
.dg-product .bg-img .product-item .product-button a {
  text-decoration: none;
  display: flex;
  justify-content: space-around;
  font-size: 15px;
  color: #6a4b2d;
  text-transform: uppercase;
  align-items: center;
}
.dg-product .bg-img .product-item .product-button a i {
  font-size: 20px;
}
@media (max-width: 575.98px) {
  .dg-product .bg-img .product-item h4 {
    font-size: 12px;
  }
  .dg-product .bg-img .product-item h2 {
    font-size: 30px;
  }
  .dg-product .bg-img .product-item .product-button a {
    justify-content: center;
  }
  .dg-product .bg-img .product-item .product-button a i {
    padding-left: 0.5rem;
  }
}

.dg-ship {
  padding: 90px 0;
  text-align: center;
}
.dg-ship h4 {
  text-transform: uppercase;
  font-size: 14px;
  line-height: 22px;
  font-weight: 600;
  color: #999999;
}
.dg-ship h2 {
  font-size: 50px;
  font-weight: 400;
  color: #333333;
}
.dg-ship .ship-item {
  margin-top: 20px;
}
.dg-ship .ship-item .ship-conten h3 {
  white-space: nowrap;
  font-size: 15px;
  line-height: 24px;
  color: #6a4b2d;
}
.dg-ship .ship-item .ship-conten time, .dg-ship .ship-item .ship-conten p {
  font-size: 15px;
  line-height: 28px;
  color: #333333;
}
.dg-ship .ship-item .ship-conten p {
  color: #999999;
}
@media (max-width: 575.98px) {
  .dg-ship {
    padding: 45px 0;
  }
  .dg-ship h4 {
    font-size: 12px;
  }
  .dg-ship h2 {
    font-size: 30px;
  }
}

.dg-sp-banner img {
  width: 100%;
}

.dg-sp-body {
  padding: 90px 0;
}
.dg-sp-body h2 {
  font-size: 35px;
  font-weight: 400;
  color: #111111;
}
.dg-sp-body h3 {
  font-size: 18px;
  font-weight: 400;
  line-height: 50px;
  color: #333333;
}
.dg-sp-body ul {
  list-style: none;
  padding-left: 18px;
}
.dg-sp-body ul li a {
  font-size: 15px;
  line-height: 50px;
  color: #999999;
}
.dg-sp-body ul li.active a, .dg-sp-body ul li:hover a {
  text-decoration: underline;
  font-weight: 600;
  color: #6a4b2d;
}
.dg-sp-body .sp-item .question {
  padding: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ebebeb;
}
.dg-sp-body .sp-item .question h4 {
  font-weight: 600;
  font-size: 15px;
  line-height: 50px;
  color: #333333;
}
.dg-sp-body .sp-item .question i {
  transition: 0.5s all ease-in-out;
  transform: rotate(180deg);
}
.dg-sp-body .sp-item .question.active h4, .dg-sp-body .sp-item .question.active i, .dg-sp-body .sp-item .question:hover h4, .dg-sp-body .sp-item .question:hover i {
  color: #6a4b2d;
}
.dg-sp-body .sp-item .question.active i {
  transform: rotate(0);
}
.dg-sp-body .sp-item .conten {
  display: none;
  margin-top: 15px;
  margin-left: 20px;
  margin-bottom: 40px;
}
.dg-sp-body .sp-item .conten p {
  font-size: 15px;
  line-height: 22px;
  color: #666666;
}
@media (max-width: 767.98px) {
  .dg-sp-body {
    padding: 45px 0;
  }
  .dg-sp-body .sp-item .question h4 {
    padding: 1rem 0;
    line-height: 25px;
  }
  .dg-sp-body h3 {
    padding: 5px 10px;
    background: #745536;
    color: #fff;
  }
  .dg-sp-body ul {
    box-shadow: 0 0 1px 0 #7e5f40 inset;
  }
}

